.report-details-container {
    padding: 20px;
    background-color: #f9f5f0;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    width: 100vw; /* Ocupa todo el ancho de la pantalla */
    max-width: 100%; /* Asegura que no se quede centrado con un ancho limitado */
  }
 
  .back-button {
    background-color: #8C6239;
    color: white;
    padding: 10px;
    border-radius: 5px;
    cursor: pointer;
    border: none;
    font-size: 16px;
    margin-bottom: 20px;
    align-self: flex-start; /* Ajusta el botón a la izquierda */
  }
 
  .tutor-info {
    background-color: #f0c490;
    padding: 20px;
    border-radius: 10px;
    text-align: center;
    margin-bottom: 20px;
    width: 80%; /* Aumentar el ancho para que abarque más pantalla */
    max-width: 1000px; /* Un límite de ancho mayor */
  }
 
  .report-info {
    padding: 20px;
    border-radius: 10px;
    background-color: #f5e0c3;
    width: 80%; /* Ajustar el ancho al 80% del contenedor */
    max-width: 1000px; /* Límite de ancho para pantallas grandes */
    text-align: left;
  }
 
  table {
    width: 100%; /* Hacer que la tabla ocupe todo el ancho disponible */
    border-collapse: collapse;
  }
 
  th, td {
    border: 1px solid #ddd;
    padding: 12px;
    text-align: left;
  }
 
  th {
    background-color: #f0c490;
  }
 
  td {
    background-color: #f9f5f0;
  }
 
