body {
    font-family: 'Calibri', sans-serif;
  }
  
  .outer-container-search {
    height: calc(100vh - 12vh);
    padding-top: 30px;
    overflow-y: auto;
    margin: 0 auto;
    width: 95vw;
    max-width: 1200px;
  }
  
  .sessions-container-search {
    width: 85%;
    margin: 0 auto;
    padding: 0rem;
    background-color: #FFFFFF;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  
  .tutors-page {
    padding: 15px;
  }
  
  .header-search {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    background-color: #D9B799;
    padding: 10px;
    border-radius: 10px;
  }
  
  h2 {
    color: #8B5E3C;
    font-size: 2.5em;
  }
  
  .search-container {
    display: flex;
    align-items: center;
  }
  
  .search-input {
    padding: 15px;
    border-radius: 18px;
    border: 5px solid #ccc;
    margin-right: 10px;
    width: 100%;
    max-width: 300px;
  }
  
  .filter-dropdown {
    padding: 10px;
    border-radius: 18px;
    border: 2px solid #ccc;
    background-color: #f0d6c1;
    margin-right: 10px;
  }
  
  .content {
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
  }
  
  .tutors-list {
    flex: 1;
  }
  
  .tutor-card-searchadmin {
    display: flex;
    justify-content: space-between;
    background-color: #D9B799;
    border-radius: 10px;
    padding: 20px;
    margin-bottom: 20px;
    align-items: center;
    width: 100%;
  }
  
  .tutor-info-searchadmin {
    display: flex;
    align-items: center;
    max-width: 80%;
  }
  
  .avatar-placeholder-searchadmin {
    width: 50px;
    height: 50px;
    background-color: #fde0d6;
    border-radius: 50%;
    margin-right: 20px;
    flex-shrink: 0;
  }
  
  .stars {
    color: #8B5E3C;
  }
  
  .report-btn-searchadmin, .report-btn {
    background-color: #8B5E3C;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease; /* Agregado para una mejor experiencia de usuario */
  }
  
  .report-btn-searchadmin:hover, .report-btn:hover {
    background-color: #6a452d;
  }
  
  /* Ajustes responsivos */
  @media (max-width: 768px) {
    .header-search {
      flex-direction: column;
      align-items: flex-start;
    }
  
    .search-container {
      flex-direction: column;
      width: 100%;
      margin-top: 10px;
    }
  
    .search-input, .filter-dropdown {
      width: 100%;
      margin-bottom: 10px;
    }
  
    .content {
      flex-direction: column;
      align-items: center;
    }
  
    .tutors-list {
      width: 100%;
    }
  
    .tutor-card-search {
      width: 100%;
      max-width: 400px;
      flex-direction: column;
      text-align: center;
    }
  
    .tutor-info-search {
      flex-direction: column;
      align-items: center;
    }
  
    .report-btn {
      width: 100%;
    }
  }
  